html {
    overflow:hidden;
}
#root, html {
    background-color: #3B556D;
    font-family: Courier New, monospace;
}


.main_card {
    box-shadow: 20px 15px 5px #1C2942;
    background: linear-gradient(#0B162C, #AD956B, #0B162C) !important;
    background-repeat: no-repeat;
    background-size:cover;

    border-top-left-radius: 5px;
    border-top-right-radius: 5px;

    margin-top: 5vh;
    margin-bottom: 5vh;
    margin-left: 5vw;
    margin-right: 5vw;

    width: 92vw;
    max-height: 92vh;
    overflow:scroll;

    animation: anim_home ease-in 2.5s;
}
@keyframes anim_home {
    0% {
        background: linear-gradient(#0B162C, #AD956B, #0B162C) !important;
        background-repeat: no-repeat;
        background-size:cover;

        border-top-left-radius: 5px;
        border-top-right-radius: 5px;

        box-shadow: 0px 0px 0px #1C2942;
        margin-top: 5vh;
        margin-bottom: 5vh;
        margin-left: 5vw;
        margin-right: 5vw;

        opacity: 0.05;
        width: 70vw;
        max-height: 92vh;
    }
    100% {
        background: linear-gradient(#0B162C, #AD956B, #0B162C) !important;
        background-repeat: no-repeat;
        background-size:cover;

        border-top-left-radius: 5px;
        border-top-right-radius: 5px;


        box-shadow: 20px 15px 5px #1C2942;
        margin-top: 5vh;
        margin-bottom: 5vh;
        margin-left: 5vw;
        margin-right: 5vw;

        opacity: 1;
        width: 75vw;
        max-height: 92vh;
    }
}



/* main card about */
.main_card_about {
    box-shadow: 20px 15px 5px #1C2942;
    background-color: #0B162C !important;
    /* background-color: #0B162C;  MODE NUIT */
    background-repeat: no-repeat;
    background-size:cover;

    border-top-left-radius: 5px;
    border-top-right-radius: 5px;

    margin-top: 5vh;
    margin-bottom: 5vh;
    margin-left: 5vw;
