/* global font */
p,h1 {
    color: #5FC2BA;
}

/* Nav  [patch : contact child --> late, end of align]*/
.navbar {
    margin-right: 5vw;
    background-color: inherit;
    animation: navbar linear 2s;
}
@keyframes navbar {
    0% {
        width: 10%;
        margin-top: -300%;
    }
    100% {
        width: 100%;
        margin-right: 5vw;
        background-color: inherit;
        margin-top: 0%;
    }
}

.nav_btn {
    color: #5FC2BA;
    text-decoration: none;
    transition: all 1s linear;
}
.nav_btn:hover {
    color: #5FC2BA !important;
    background: linear-gradient(#0b1b3a, #0B162C);
    border-radius: 18px;
    padding: 5px 5px 2px 5px;
    box-shadow: 0px 5px 0px #6aa077;
}
.nav_btn > a {
    color: #5FC2BA;
    text-decoration: none;
}
.targeted_component {
    color: #B0B9A8 !important;
    font-size:medium;
    transition: all 1s linear;
}
.targeted_component:hover {
    color: #B0B9A8 !important;
    font-size:medium;
}

/*  btn cv */
.btn_cv {
    font-family: Impact, fantasy;
    font-size: medium;
    color: #AA9D9F;
    border-style: solid;
    border-width: 1px;
    border-color: #AA9D9F;
    background-color: inherit;
    border-radius: 15px;
    padding: 1vh 4vh 1vh 4vh;
    margin-top: 1vh;
    transition: all 1s;

    animation: btn_cv_anim linear 1.8s;
}
@keyframes btn_cv_anim {
    0% {
        margin-left: -300%;
    }
    100% {
        margin-right: 5vw;
        background-color: inherit;
        margin-left: 0%;
    }
}
.btn_cv:hover {
    font-family: Impact, fantasy;
    font-weight:lighter;
    color: #AA9D9F;
    border-style: solid;
    border-width: 3px;
    border-color: #AA9D9F;
    background: linear-gradient( #AD956B, #0B162C) !important;
    border-radius: 15px;
    padding: 1vh 2vh 1vh 2vh;
    margin-top: 1vh;
}

/* btn_projets */
.btn_projets {
    font-family: Impact, fantasy;
    font-size: medium;
    color: #5FC2BA;
    box-shadow: 5px 7px 2px #5FC2BA;
    border-style: solid;
    border-color: #5FC2BA;
    background-color: inherit;
    border-radius: 15px;
    padding: 1vh 4vh 1vh 4vh;
    margin-top: 1vh;
    transition: all 1s;

    animation: btn_projets_anim linear 5.5s;
}
@keyframes btn_projets_anim {
    0% {
        margin-top: 300%;
        margin-right: 1vw;
    }
    100% {
        margin-right: 1vw;
        background-color: inherit;
        margin-top: 0%;
    }
}
.btn_projets:hover {
    font-family: Impact, fantasy;
    box-shadow: 0px 0px 0px #85b18f;
    font-weight: bold;
    color: #0B162C;
    border-style: solid;
    border-color: #0B162C;
    background-color: #5FC2BA;
    border-radius: 15px;
    padding: 1vh 4vh 1vh 4vh;
    margin-top: 1vh;
}

/* _article */
._article_left {
    font-family: Georgia, serif;
    margin: 5vh 2vh 5vh 10vh;
}
._article_left > h1 {
    font-size:xx-large;
    font-weight: bolder;

    animation: h1_anim linear 4s;
}
@keyframes h1_anim {
    0% {
        margin-left: -300%;
        opacity: 5%;
    }
    100% {
        margin-left: 0%;
        opacity: 100%;
        background-color: inherit;
    }
}
._article_left > h2 {
    font-size:x-large;
    font-weight:lighter;
    color: #B0B9A8;

    animation: h2_anim linear 4s;
}
@keyframes h2_anim {
    0% {
        margin-left: -300%;
        opacity: 5%;
    }
    100% {
        margin-left: 0%;
        opacity: 100%;
        background-color: inherit;
    }
}
._article_left > p {
    color: #B0B9A8;

    animation: p_anim linear 6s;
}
@keyframes p_anim {
    0% {
        opacity: 0%;
    }
    100% {
        opacity: 100%;
        background-color: inherit;
    }
}
._article_right {
    /*  border debugg */
    /* background-color: red;
    border: solid yellow 3px; */

    font-family: Georgia, serif;
    margin: 5vh 5vh 5vh 5vh;
}

._citation {
    color: #C9E3CC;
}

.ia_img {
    opacity: 50%;
    margin-top: -5vw;
    width: 90vw;
    height: 60vh;
    transition: all 2s;
    animation-iteration-count: 1;
    animation: ia_img_anim 30s 1 linear;
}
@keyframes ia_img_anim {
    1% {
        opacity: 50%;
        width: 90vw;
        height: 60vh;
        margin-right: 0vw;
    }
    5% {
        opacity: 5%;
        width: 40vw;
        height: 30vh;
        margin-right: -300%;
    }
    10% {
        opacity: 50%;
        width: 90vw;
        height: 60vh;
        margin-top: -5vw;
        margin-right: 0%;
    }
    20% {
        opacity: 50%;
        width: 90vw;
        height: 60vh;
        margin-right: 0vw;
    }
    30% {
        margin-right: -3vw;
        transform: rotate(3deg);
    }
    40% {
        margin-right: 6vw;
        transform: rotate(6deg);
    }
    50% {
        margin-right: -4vw;
        transform: rotate(-4deg);
    }
    75% {
        margin-right: 5vw;
        transform: rotate(5deg);
    }
    90% {
        margin-right: -6vw;
        transform: rotate(6deg);
    }
    100% {
        opacity: 50%;
        width: 90vw;
        height: 60vh;
        margin-right: 0vw;
    }
}






