#contact_title {
    color:#F7F6CF;
    border: outset 3px #F7F6CF;
    padding: 1vh;
    border-bottom-left-radius: 9px;
    border-bottom-right-radius: 9px;
    border-width: 0px 0px 3px 0px;
    transition: all 0.5s ease-in;
}
#contact_title:hover {
    color:#F7F6CF;
    border: outset 3px #F7F6CF;
    padding: 1vh;
    padding-left: 2vh;
    padding-right: 2vh;
    border-top-left-radius: 9px;
    border-top-right-radius: 9px;
    border-width: 0px 3px 0px 3px;
}

.contact_item {
    font-size: x-large;
    font-weight: bold;
    animation: drop_title ease-in 2s;
}
@keyframes drop_title {
    0% {
        margin-right: -300%;
    }
    100% {
        margin-right: 0%;
    }
}

/* child */
.anim_box {
    margin-top: 10vh;
    animation: load_box ease-in 1.5s;
}

@keyframes load_box {
    0% {
        margin-left: -300%;
    }
    100% {
        margin-left: 0%;
    }
}

.item_grp {
    background-color: inherit;
}
.item_grp > div {
    margin-left: 1vh;
    margin-right: 1vh;
}
/* --- */
.item_1 {
    font-weight: bold;

    animation: load_item_1 ease-in 1.5s;
}
.item_1:hover {
    font-weight: bold;

    animation: load_item_1 ease-in 1.5s;
}

@keyframes load_item_1 {
    0% {
        margin-top: -300%;
    }
    100% {
        margin-top: 0%;
    }
}

.item_2 {
    font-weight: bold;
    color: #F7F6CF;

    animation: load_item_2 ease-in 1.5s;
}

@keyframes load_item_2 {
    0% {
        margin-top: 300%;
    }
    100% {
        margin-top: 0%;
    }
}
/* centre */
.item_3 {
    font-weight: bold;
    font-size: medium;
    color: white;
    align-content: center;

    animation: load_item_3 ease-in 3.5s;
}
.contact_profil_img {
    width: 20vw;
    height: 20vh;
}
.contact_mail_img {
    width: 10vw;
    height: 10vh; 
}
.contact_github_img {
    width: 10vw;
    height: 10vh;
}

@keyframes load_item_3 {
    0% {
        margin-top: 300%;
        width: 2vw;
        height: 2vh;
    }
    100% {
        margin-top: 0%;
        width: 15vw;
        height: 15vh;
    }
}

.item_4 {
    font-weight: bold;
    color: green;
    animation: load_item_4 ease-in 1.5s;
}

@keyframes load_item_4 {
    0% {
        margin-top: -300%;
    }
    100% {
        margin-top: 0%;
    }
}

.item_5 {
    font-weight: bold;
    color: #F7F6CF;

    animation: load_item_5 ease-in 1.5s;
}

@keyframes load_item_5 {
    0% {
        margin-top: 300%;
    }
    100% {
        margin-top: 0%;
    }
}

.contact_github_img:hover {
    transition: all 0.3s ease-in;
    width: 20vh;
}